* {
  margin: 0;
  padding: 0;
}

body {
  background-color: #333;
}
.groupComponentDiv {
  display: flex;
  width: 100%;
  justify-content:space-around;
}
.group {
  /* border: 2px solid white; */
  width: 500px;
  height: 450px;
}

.left-names{
  margin-top: 20px;
  margin-right: 45px;
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.left-names span, .bottom-names span {
  display: block;
  width: 70px;
  height: 70px;
  background-color: #fff;
  word-wrap: break-word;
  font-size: 14px;
  border-radius: 50%;
  padding: 10px;
  box-sizing: border-box;
}

.bottom-names{
  margin-top: 25px;
  display: flex;
  flex-direction: row;
  margin-left: 120px;
  gap: 40px;
}

.group ul {
  margin-top: 20px;
  display: flex;
  flex-direction: row;
  list-style: none;
  gap: 30px;
}

.group ul li {
  display: block;
  width: 75px;
  height: 75px;
  border: 2px solid #fff;
}

.left-names_group-setka{
  display: flex;
}

.about-page {
  display: flex;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 80px;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  font-weight: bold;
  color: #fff;
  width: 80%;
  height: 100px;
  background-color: #3E8FA0;
}
button{
  display: block;
  margin-left: 25%;
  width: 20%;
  height: 50px;
  background-color: #fff;
  color: #333;
  text-align: center;
  margin-top: 30px;
  border-radius: 20px;
}
button:hover {
  transform: scale(1.1);
  cursor: grab;
}